.social {
  width: 280px;
  font-family: @font-header;
  align-self: center;
  padding-bottom: 16px;
  padding-top: 20px;
  border-top: 1px solid @social-lines;
  border-bottom: 1px solid @social-lines;
}

.social__list {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: center;
}

.social__img-vk {
  width: 23px;
  height: 16px;
}

.social__img-inst {
  width: 16px;
  height: 16px;
}

.social__img-fb {
  width: 21px;
  height: 21px;
}

.social__item {
  margin-left: 35px;
  align-self: center;
  cursor: pointer;
  stroke-width: 1.1px;
  fill: @social;
  transition: fill @trans--300;

  &:nth-of-type(3) {
    stroke: @social;
    fill: none;
    transition: stroke @trans--300;

    &:hover {
      stroke: @basic-theme;
      fill: none;
    }
  }

  &:hover {
    fill: @basic-theme;
  }

  &:active {
    fill: @basic-theme;
    opacity: 0.5;
  }
}

.social__item:first-of-type {
  margin-left: -5px;
}

@media (min-width: @tablet-width) {
  .social__list {
    &::after,
    &::before {
      display: none;
    }
  }

  .social__item {
    margin-left: 25px;
  }

  .social__item:first-of-type {
    margin-left: 0;
  }

  .social {
    margin-bottom: 0;
    align-self: center;
    border: none;
    margin-left: 115px;
  }

  .social__img-vk {
    width: 29px;
    height: 17px;
  }

  .social__img-inst {
    width: 23px;
    height: 23px;
  }

  .social__img-fb {
    width: 27px;
    height: 27px;
  }
}

@media (min-width: @desktop-width) {
  .social__list {
    margin-left: 0;
  }

  .social {
    margin-left: 180px;
    padding-top: 18px;
  }
}
