.programs {
  display: flex;
  flex-wrap: wrap;
  width: 280px;
  align-self: center;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: @tablet-width) {
  .programs {
    padding: 0;
    width: 708px;
    flex-direction: row;
    box-sizing: border-box;
  }
}

@media (min-width: @desktop-width) {
  .programs {
    width: 1220px;
    box-sizing: border-box;
    margin-top: 80px;
    grid-column-start: 1;
    grid-column-end: -1;
  }
}
