.main-nav {
  background-color: #ffffff;
  color: #000000;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  font-family: "Oswald", "Arial", sans-serif;
  grid-template-columns: repeat(1, 320px);
  column-gap: 20px;
  grid-column-start: 1;
  grid-column-end: -1;
}
.main-nav-inactive {
  display: none;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.main-nav__link {
  text-decoration: none;
  color: #000000;
  font-weight: 500;
}
.main-nav__item {
  width: 320px;
  align-self: center;
  border-top: 1px solid #e6e6e6;
  padding-top: 22px;
  padding-bottom: 23px;
  text-align: center;
}
.main-nav__item:not(.main-nav__loc-item) {
  transition: opacity 0.3s ease;
}
.main-nav__item:not(.main-nav__loc-item):hover {
  opacity: 0.6;
}
.main-nav__item:not(.main-nav__loc-item):active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    margin-left: 95px;
    width: 440px;
    flex-wrap: wrap;
    justify-content: space-between;
    align-self: center;
    margin-top: 18px;
  }
  .main-nav__list {
    display: flex;
    padding: 0;
    margin: 0;
    flex-wrap: wrap;
    flex-direction: row;
  }
  .main-nav__link {
    font-size: 18px;
    line-height: 24px;
  }
  .main-nav__item {
    width: auto;
    min-width: 64px;
    margin-left: 35px;
    border: none;
    padding-top: 0;
    padding-bottom: 8px;
  }
  .main-nav__item:first-of-type {
    margin-left: 0;
  }
  .main-nav__loc-item {
    border-bottom: 2px solid #68b738;
  }
  .main-nav__loc-index-item {
    border-bottom: 2px solid #68b738;
  }
}
@media (min-width: 1440px) {
